//å¼•å…¥
import Vue from "vue";
import VueRouter from "vue-router";
//å¯ç”¨
Vue.use(VueRouter);

// è·¯ç”±å™¨éœ€è¦ä¸€ä¸ªæ ¹ç»„ä»¶,App.vueä¸ºæ ¹ç»„ä»¶ã€‚å°±æ˜¯æŒ‚è½½è·¯ç”±çš„åœ°æ–¹ã€‚
// var App = Vue.extend(require('./App.vue'));
var App = Vue.extend(require('./App.vue'));
// è·¯ç”±é…ç½®å‚æ•°
var router = new VueRouter({
    hashbang: true //hashè·¯ç”±
});


export default function () {

    //å®šä¹‰è·¯ç”±è§„åˆ™
    router.map({
        '/index': {
            name: 'index',
            component: require('./views/blog.index.vue')
        },
        '/blog': {
            name: 'blog',
            component: require('./views/blog.vue'),
            subRoutes: {
                '/art-list': {
                    name: 'artList',
                    component: require('./views/blog.articleList.vue')
                },
                '/his-list': {
                    name: 'historyList',
                    component: require('./views/blog.historyList.vue')
                },
                '/tag-list': {
                    name: 'tagList',
                    component: require('./views/blog.tagList.vue')
                }
            }
        },
        '/article/:articleId': {
            name: 'article',
            component: require('./views/blog.article.vue')
        },
    });

    //è·¯ç”±é‡å®šå‘
    router.redirect({
        '/': '/index',
        '/blog': '/blog/art-list/?listType=latest',
    });



    //è·¯ç”±åˆ‡æ¢å‰
    router.beforeEach(({to, from, next}) => {
        let toPath = to.path
        let fromPath = from.path
        console.log('from: ' + fromPath + ' to: ' + toPath)
        if (toPath.replace(/[^/]/g, '').length > 1) {
            router.app.isIndex = false
        }
        else {
            let depath = toPath === '/' || toPath === '/invite' || toPath === '/rank'
            router.app.isIndex = depath ? 0 : 1
        }
        next()
    })

    //è·¯ç”±åˆ‡æ¢åŽ
    router.afterEach(function ({to}) {
        // console.log(`æˆåŠŸæµè§ˆåˆ°: ${to.path}`)
        // $.refreshScroller()
    });


    //åœ¨æ ¹ç»„ä»¶ä¸Šå¯åŠ¨è·¯ç”±,æŒ‚è½½ç‚¹ä½ä¸ºbodyä¸Š
    router.start(App, '#app');
}


