import Vue from "vue";
import Vuex from "vuex";
import mod_myinfo from "./modules/mod_myinfo";
import mod_article from "./modules/mod_article";
import mod_comment from "./modules/mod_comment";
import mod_tag from "./modules/mod_tag";
import {
    SET_SHOWMYWORD_STATUS,
    SET_SOCIALIMG,
} from './mutation-types'
//vuexæ¨¡å—
// å‘Šè¯‰ vue â€œä½¿ç”¨â€ vuex
Vue.use(Vuex);

// æ•´åˆåˆå§‹çŠ¶æ€å’Œå˜æ›´å‡½æ•°ï¼Œæˆ‘ä»¬å°±å¾—åˆ°äº†æˆ‘ä»¬æ‰€éœ€çš„ store
// è‡³æ­¤ï¼Œè¿™ä¸ª store å°±å¯ä»¥è¿žæŽ¥åˆ°æˆ‘ä»¬çš„åº”ç”¨ä¸­

export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    //å…¨å±€stateçŠ¶æ€
    state: {
        isShowMyWords: false,
        socialImgUrl:'',
    },
    //å…¨å±€çªå˜äº‹ä»¶å¤„ç†
    mutations: {
        //toggleæˆ‘çš„ä¸ªäººç§°è¿°ä¿¡æ¯
        [SET_SHOWMYWORD_STATUS] (state) {
            state.isShowMyWords = !state.isShowMyWords;
        },
        //è®¾ç½®æˆ‘çš„ç¤¾äº¤å¼¹å‡ºç»„ä»¶çš„img
        [SET_SOCIALIMG] (state,url) {
            state.socialImgUrl = url;
        },
    },
    modules: {
        mod_myinfo,//æˆ‘çš„ä¿¡æ¯
        mod_article,//æ–‡ç« åˆ—è¡¨
        mod_tag,//æ–‡ç« åˆ—è¡¨
        mod_comment,//è¯„è®º
    }
})


