export const addImgPrefix = function (imgName) {
    if (!!imgName && imgName.indexOf('http') === -1) {
        //æ­£ç¡®çš„æ—¶é—´æˆ³
        return `${CONFIG.imgResource}${imgName}`;
    } else if (!imgName) {
        return false;
    } else {
        return imgName;
    }
}


